body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: var(--background-color);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
@media (min-width: 500px) {
	#container-left img {
		height: 50px;
		width: 20px;

		border-radius: 50px;
		box-shadow: 5px 2px 20px black;
	}
}

:root {
	--background-color: #faf9dc;

	--primary-color: #0afa56;
	--secondary-color: #2093fa;
	--light-color: #fac670;
	--extra-color: #9c55fa;
	--heading-font: "Bungee", cursive;
	--paragraph-font: "Signika Negative", sans-serif;
	--naming-font: "Itim", cursive;
	--special-font: "Spline Sans Mono", monospace;
	--modern-font: "Patrick Hand SC", cursive;
}
.footer {
	background-color: #2093fa;
	background-color: var(--secondary-color);
	margin: 0 auto;
}
#social-media-container {
	/* background-color: red; */
	margin: 20px 50px;
	margin-left: 0px;
	display: grid;

	grid-template-columns: 1fr 1fr;
	/* gap: 20px; */
	justify-content: center;
	padding-top: 20px;
	width: 100%;
}
.whatsapp a {
	color: #0afa56;
	color: var(--primary-color);
}
.social-media {
	padding: 20px;
	font-style: "Itim", cursive;
	font-style: var(--naming-font);

	/* border-radius: 50%; */
	/* background-color: black; */
}
#copyright {
	justify-content: center;
	align-items: center;
	display: flex;

	font-size: 16px;
	font-family: "Itim", cursive;
	font-family: var(--naming-font);
}
.title {
	font-family: "Itim", cursive;
	font-family: var(--naming-font);
	font-size: 16px;
}
.social-media a {
	text-decoration: none;

	font-size: 20px;
	padding: 0 12px;
	color: rgb(34, 34, 34) !important;
}
.social-media-link {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	font-size: 10px;
}

@media (min-width: 320px) {
	.social-media a,
	.title {
		font-size: 22px;
	}
}
@media (min-width: 420px) {
	#social-media-container {
		grid-template-columns: 1fr 1fr 1fr;
	}
}
@media (min-width: 540px) {
	.social-media a {
		font-size: 45px;
	}
	#social-media-container {
		grid-template-columns: 1fr 1fr 1fr 1fr;
		max-width: 737px;
		margin: 0 auto;
	}
}
@media (min-width: 918px) {
	.social-media a {
		font-size: 55px;
	}
	#social-media-container {
		grid-gap: 40px;
		gap: 40px;
	}
}


.gallery-container {
	margin: 0 auto;
	justify-content: center;
	text-align: center;
}
.gallery-main-container {
	margin: 0 auto;
	max-width: 900px;
	/* margin-left: 50px; */
	background-color: azure;
}
.gallery-grid {
	display: grid;
	grid-template-columns: 1fr;
	height: 100%;
	grid-column-gap: 20px;
	-webkit-column-gap: 20px;
	        column-gap: 20px;
	grid-row-gap: 50px;
	row-gap: 50px;
}
.gallery-title {
	font-family: var(--modern-font);
}

.image img {
	width: 85%;
	height: 200px;
}
.title {
	font-size: 20px;
}
@media (min-width: 490px) {
	/* .image img {
		width: 320px;
		height: 200px;
	} */
	.gallery-grid {
		grid-template-columns: 1fr 1fr;
	}
}
@media (min-width: 750px) {
	.gallery-grid {
		grid-template-columns: 1fr 1fr 1fr;
	}
}

#main-container {
	max-width: 75%;
	margin: 0 auto;
