#container-left span {
	display: block;
	text-align: center;
	margin-left: 20px;
	font-family: var(--heading-font);
	font-size: 14px !important;
	/* font-weight: 400 !important; */
}

@media (min-width: 378px) {
	#container-left img {
		height: 85px;
		width: 80px;
	}
