	background-color: var(--primary-color);
	width: 100%;
	height: 100px;
	display: flex;
	flex-wrap: wrap-reverse;
	grid-gap: 2rem;
	gap: 2rem;
	justify-items: center;
	/* justify-content: space-around; */
	align-items: center;
	justify-content: space-between;
	padding: 20px 0;
	/* box-shadow: -2px 2px 2px rgb(79, 77, 77); */
}

#left-header {
	display: flex;
	justify-content: left;
}
#right-header {
	display: flex;
	justify-content: end;
}

